import Vue from 'vue';
import Vuex from 'vuex';
import moment from 'moment';
import axios from 'axios';
// eslint-disable-next-line no-unused-vars
import VueAxios from 'vue-axios';
import { Line } from 'vue-chartjs';
import 'chartjs-plugin-zoom';
import line2 from '../components/line2.vue';


Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    totalData_table: [],
    midataerrortable: [],
    midatalinechart: [],
    midatabarchart: [],
    midatatreemap: [],
    isLoading: true,
    resetEnv: true,
  },
  mutations: {
    setLoading(state, data) {
      state.isLoading = data;
    },

  },
  actions: {
    resetEnv_ch({ state }) {
      if (state.resetEnv === true) {
        console.log('aaaaaa');
        state.resetEnv = false;
      } else {
        state.resetEnv = true;
      }
    },
    getData({ state, commit }) {
      const con = axios.get(`${process.env.VUE_APP_LOCAL}`);
      con.then((response) => {
        console.log(response.data.MITotalData);
        response.data.MITotalData.forEach((e) => {
          state.totalData_table.push({
            date: moment(new Date(e.date)).format('YYYY-MM-DD'),
            classroom: e.classroom,
            host: e.host,
            use_total_time: e.use_total_time.toFixed(2),
            count: e.count,
            per_average_time: e.per_average_time.toFixed(2),
          });
        });
        state.midataerrortable = response.data.midataerrortable;
        state.midatalinechart = response.data.midatalinechart;
        state.midatabarchart = response.data.midatabarchart;
        state.midatatreemap = response.data.midatatreemap;
      });
      return con;
    },
    reset() {
      const aaaa = () => {
        console.log(line2);
      };
      return aaaa();
    },
  },
  modules: {
  },
});
